import * as XLSX from "xlsx";

//  Excel 
export const parseExcelFile = (selectedSheetIndex, callback) => {
  //  fetch  "./xlsx/voc.xlsx"  Excel 
  fetch("./xlsx/voc.xlsx")
    .then((response) => response.arrayBuffer())  //  ArrayBuffer 
    .then((data) => {

      //  XLSX.read  ArrayBuffer  data Workbook
      const workbook = XLSX.read(new Uint8Array(data), { type: "array" });

      // 
      const sheetName = workbook.SheetNames[selectedSheetIndex];

      // Worksheet
      const worksheet = workbook.Sheets[sheetName];

      //  XLSX.utils.sheet_to_json  JSON 
      //  header: 1  JSON 
      const parsedData = XLSX.utils.sheet_to_json(worksheet, { header: 1 });

      //  1 JSON 
      const wordsData = parsedData.slice(1).map((row) => ({
        vocabulary: row[0],
        part_of_speech: row[1],
        zh_cn: row[2],
      }));

      // 
      callback(wordsData);
    })
    .catch((error) => {
      console.error("Error reading voc.xlsx:", error);
    });
};


