import React, { useState, useEffect } from "react";
import Card from "./Card";
import { parseExcelFile } from "./excelUtils"; // excelUtils.js

const Display = () => {

  // 
  const [cardDataArray, setCardDataArray] = useState([]);
  // xls 
  const [selectedSheetIndex, setSelectedSheetIndex] = useState(0);

  //  handleSheetChange 
  const handleSheetChange = (index) => {
    // 
    const resetCards = cardDataArray.map((data) => ({ ...data, isFlipped: false }));
    setCardDataArray(resetCards);

    //  index  useEffect
    setSelectedSheetIndex(index);
  };

  useEffect(() => {
    //  voc.xlsx  cardDataArray
    parseExcelFile(selectedSheetIndex, (data) => {
      //  cardDataArray data
      setCardDataArray(data);
    });
  }, [selectedSheetIndex]); //  selectedSheetIndex 



  return (
    <div className="card-container">
      {/*  */}
      <div className="sheet-buttons">
        <button onClick={() => handleSheetChange(0)}>1</button>
        <button onClick={() => handleSheetChange(1)}>2</button>
        <button onClick={() => handleSheetChange(2)}>3</button>
        <button onClick={() => handleSheetChange(3)}>4</button>
        <button onClick={() => handleSheetChange(4)}>5</button>
        <button onClick={() => handleSheetChange(5)}>6</button>
      </div>
      {cardDataArray.map((data, index) => (
        <Card key={index} data={data} index={index} />
      ))}


    </div>
  );
};

export default Display;


